import React from "react";
import {  Switch, Route } from "react-router-dom";

import TodaysWeather from "../TodaysWeather/TodaysWeather";
import Prognosis from "../Prognosis/Prognosis";

class Main extends React.Component {

    render() {
        const { weather, unitType } = this.props;
            return (
                <main>
                    <Switch >
                        <Route exact path="/" render={() => <TodaysWeather unitType={unitType} weather={weather}/>}/>
                        <Route path="/today" render={() => <TodaysWeather unitType={unitType} weather={weather}/>}/>
                        <Route path="/week" render={() => <Prognosis unitType={unitType} weather={weather}/>}/>
                    </Switch>
            </main>
        )
    }
}

export default Main;